.image-text-container {
    width: 100%;

    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
}

.image-text-image {
    max-height: 100%;
    max-width: 40%;
    display: flex;
    justify-content: center;
}

.image-text-text {
    width: 500px; /* temp fix to force this as constant to ensure text doesnt move on transition */
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.image-text-body {
    font-size: 1.5vw;
    width: 100%;
    text-wrap: wrap;
    text-align: left;
    text-justify: none;
    line-height: 2.2vw;
}